import langEn from '../utils/lang/en-us'
import langZh from '../utils/lang/zh-cn'

const metaMaskInfo = {
  data () {
    return {
      metaInfo: {
        info: langEn.commom.info,
        warning: langEn.commom.warning,
        success: langEn.commom.success,
        error: langEn.commom.error,
        status1: langEn.promptMessage.metaStatus1,
        status2: langEn.promptMessage.metaStatus2,
        status3: langEn.promptMessage.metaStatus3,
        status4: langEn.promptMessage.metaStatus4,
        status5: langEn.promptMessage.metaStatus5,
        networkVersion: langEn.promptMessage.networkVersion,
        recommendedAddress: langEn.promptMessage.recommendedAddress,
        checkAddress: langEn.promptMessage.checkAddress,
        checkVal: langEn.promptMessage.checkVal,
        checkFloat: langEn.promptMessage.checkFloat,
        loginWallet: langEn.promptMessage.loginWallet,
        loadBalance: langEn.promptMessage.loadBalance,
        checkBalance: langEn.promptMessage.checkBalance,
        isSupported: langEn.promptMessage.isSupported,
        supportedTime: langEn.promptMessage.supportedTime,
        createAddress: langEn.promptMessage.createAddress,
        copySuccess: langEn.promptMessage.copySuccess,
        copyFail: langEn.promptMessage.copyFail,
        infoAddress: langEn.promptMessage.infoAddress
      }
    }
  },
  watch: {
    '$i18n.locale': {
      handler: function (newVal) {
        const commomEn = langEn.commom
        const commomZh = langZh.commom
        const prompEn = langEn.promptMessage
        const prompZh = langZh.promptMessage
        if (newVal === 'en') {
          this.metaInfo.info = commomEn.info
          this.metaInfo.warning = commomEn.warning
          this.metaInfo.success = commomEn.success
          this.metaInfo.error = commomEn.error
          this.metaInfo.status1 = prompEn.metaStatus1
          this.metaInfo.status2 = prompEn.metaStatus2
          this.metaInfo.status3 = prompEn.metaStatus3
          this.metaInfo.status4 = prompEn.metaStatus4
          this.metaInfo.status5 = prompEn.metaStatus5
          this.metaInfo.networkVersion = prompEn.networkVersion
          this.metaInfo.recommendedAddress = prompEn.recommendedAddress
          this.metaInfo.checkAddress = prompEn.checkAddress
          this.metaInfo.checkVal = prompEn.checkVal
          this.metaInfo.checkFloat = prompEn.checkFloat
          this.metaInfo.loginWallet = prompEn.loginWallet
          this.metaInfo.loadBalance = prompEn.loadBalance
          this.metaInfo.checkBalance = prompEn.checkBalance
          this.metaInfo.isSupported = prompEn.isSupported
          this.metaInfo.supportedTime = prompEn.supportedTime
          this.metaInfo.createAddress = prompEn.createAddress
          this.metaInfo.copySuccess = prompEn.copySuccess
          this.metaInfo.copyFail = prompEn.copyFail
          this.metaInfo.infoAddress = prompEn.infoAddress
        } else {
          this.metaInfo.info = commomZh.info
          this.metaInfo.warning = commomZh.warning
          this.metaInfo.success = commomZh.success
          this.metaInfo.error = commomZh.error
          this.metaInfo.status1 = prompZh.metaStatus1
          this.metaInfo.status2 = prompZh.metaStatus2
          this.metaInfo.status3 = prompZh.metaStatus3
          this.metaInfo.status4 = prompZh.metaStatus4
          this.metaInfo.status5 = prompZh.metaStatus5
          this.metaInfo.networkVersion = prompZh.networkVersion
          this.metaInfo.recommendedAddress = prompZh.recommendedAddress
          this.metaInfo.checkAddress = prompZh.checkAddress
          this.metaInfo.checkVal = prompZh.checkVal
          this.metaInfo.checkFloat = prompZh.checkFloat
          this.metaInfo.loginWallet = prompZh.loginWallet
          this.metaInfo.loadBalance = prompZh.loadBalance
          this.metaInfo.checkBalance = prompZh.checkBalance
          this.metaInfo.isSupported = prompZh.isSupported
          this.metaInfo.supportedTime = prompZh.supportedTime
          this.metaInfo.createAddress = prompZh.createAddress
          this.metaInfo.copySuccess = prompZh.copySuccess
          this.metaInfo.copyFail = prompZh.copyFail
          this.metaInfo.infoAddress = prompZh.infoAddress
        }
      },
      immediate: true
    }
  }
}
export {metaMaskInfo}



